import {fetchRequest, setError, subscribeUrl} from "./fetchTools";
import {
    CLEAR_SUBSCRIBE_ERROR,
    CLEAR_SUBSCRIBE_MESSAGE, SUBSCRIBE_FAIL,
    SUBSCRIBE_LOADING_START,
    SUBSCRIBE_SUCCESS
} from "../types";

export const subscribe = (formData,onResetForm,isFromPopup) => async (dispatch) => {
    dispatch({type: SUBSCRIBE_LOADING_START})
    try {
        // eslint-disable-next-line no-unused-vars
        const fetchData = await fetchRequest(subscribeUrl,'POST', JSON.stringify(formData))
        dispatch({type: SUBSCRIBE_SUCCESS})

        if (!isFromPopup){
            setTimeout(() => {
                dispatch(clearSubscribeMessage())
                onResetForm()
            }, 3000)
        }
    } catch (err) {
        dispatch(setSubscribeError(err?.message))
        if(!isFromPopup){
            setTimeout(() => {
                onResetForm()
                dispatch(clearSubscribeError())
            }, 3000)
        }
    }
}

export const clearSubscribeMessage = () => dispatch => {
    dispatch({type: CLEAR_SUBSCRIBE_MESSAGE})
}

export const clearSubscribeError = () => dispatch => {
    dispatch({type: CLEAR_SUBSCRIBE_ERROR})
}

export const setSubscribeError = (payload) => dispatch => {
    dispatch(setError(payload,SUBSCRIBE_FAIL))
}