import React from 'react';

import styles from "./SubscribeForm.module.scss"
import {useDispatch, useSelector} from "react-redux";
import {useFormValue} from "../../../hooks/useFormValue";
import {setSubscribeError, subscribe} from "../../../redux/action/subscribe";

function SubscribeForm({className,isFromPopup}) {
    const {error,loading,subscribeMessage} = useSelector(state => state.subscribe)
    const {formData, onChange,onResetForm} = useFormValue({
        email: '',
    }, setSubscribeError, error)
    const dispatch = useDispatch()

    const onSubmit = (e) => {
        e.preventDefault()
        dispatch(subscribe(formData,onResetForm,isFromPopup))
    }
    const errorTextState = error?.email ? error?.email.join() : error

    return (
        <form
            onSubmit={onSubmit}
            className={`${styles["subscribeForm"]} ${className || ''}`}
        >
            <input
                name='email'
                onChange={onChange}
                value={formData.email}
                type="text"
                className={styles["subscribeForm__input"]}
                placeholder='Enter your email address'
            />
            <button disabled={loading} type='submit' className={`${styles["subscribeForm__btn"]} ${isFromPopup ? styles["subscribeForm__popupBtn"] : ''}`}>Subscribe</button>
                <p className={`${styles["subscribeForm__messageText"]} ${styles["subscribeForm__messageText_error"]} ${error && !isFromPopup ? styles["subscribeForm__messageText_visible"] : ''}`}>{error && error.email ? errorTextState : ''}</p>
                <p className={`${styles["subscribeForm__messageText"]} ${styles["subscribeForm__messageText_success"]} ${subscribeMessage ? styles["subscribeForm__messageText_visible"] : ''}`}>Successfully
                    sent</p>
                <p className={`${styles["subscribeForm__popupMessageText"]} ${error && isFromPopup ? styles["subscribeForm__popupMessageText_visible"] : ''}`}>{error && error.email ? errorTextState : ''}</p>
        </form>
    );
}

export default SubscribeForm;