import React, {useState} from 'react';
import {Link,NavLink} from "react-router-dom";

import styles from "./Footer.module.scss"
import {footerLogo} from "../../../assets/images"
import Svg from "../../layout/Svg/Svg";
import FooterSubscribeBlock from "./FooterSubscribeBlock/FooterSubscribeBlock";

import {
    facebookIcon,
    linkedInIcon,
    behanceIcon,
    phoneIcon,
    messageIcon,
    addressIcon,
    arrowDownIcon,
    whatsappIcon,
    upworkIcon,  instagramBlackIcon
} from "../../../assets/svg";
import {
    homePagePath,
    aboutPagePath,
    appDevelopmentPath,
    blobPagePath,
    contactPagePath,
    designPagePath,
    seoPagePath,
    webDevelopmentPath,
    privacyPagePath,
    termsPagePath,
    faqPagePath
} from "../../../router/path";
import TransitionProvider from "../../../providers/TransitionProvider";
import {scrollTop, setActiveNavLinkClass} from "../../../globalFunctions";
import {contactLinks, socialLinks} from "../../../assets/UIData";

const footerLinksArr = [
    {
        title: 'Company',
        links: [
            {
                text: 'About',
                link: aboutPagePath
            },
            {
                text: 'Contact',
                link: contactPagePath
            },
            {
                text: "FAQ",
                link: faqPagePath
            },
            {
                text: 'Blog',
                link: blobPagePath
            },
            {
                text: 'Terms',
                link: termsPagePath
            },
            {
                text: 'Privacy',
                link: privacyPagePath
            }]
    },
    {
        title: 'Services    ',
        links: [
            {
                text: 'UX / UI Design',
                link: designPagePath,
            },
            {
                text: 'Web Development',
                link: webDevelopmentPath,
            },
            {
                text: "App Development",
                link: appDevelopmentPath,
            },
            {
                text: 'SEO & UX writing',
                link: seoPagePath,
            }
        ]
    },
    {
        title: 'Contact us',
        links: [
            {
                text: <>
                    <Svg className={styles['footer__linksBlockIcon']} id={phoneIcon}/>
                    +37494069650
                </>,
                link: contactLinks.phone
            },
            {
                text: <>
                    <Svg className={styles['footer__linksBlockIcon']} id={messageIcon}/>
                    info@khgtec.com
                </>,
                link: contactLinks.infoMail
            },
            {
                text: <>
                    <Svg className={styles['footer__linksBlockIcon']} id={addressIcon}/>
                    Armenia, A. Khachatryan 16/2
                </>,
                link: contactLinks.map
            },
        ]
    },
]

function Footer() {
    const [dropdownOpened, setDropdownOpened] = useState([])
    const date = new Date();

    const onToggleDropdowns = (id) => {
        !dropdownOpened.includes(id) ?
            setDropdownOpened(prevState => [...prevState, id]) :
            setDropdownOpened(prevState => (prevState.filter(item => item !== id)))
    }

    const accordionLinksActiveClass = setActiveNavLinkClass(`${styles["footer__accordionLink"]} ${styles["footer__linksBlockLink"]}`,styles["footer__linksBlockLink_active"])
    const deskLinksActiveClass = setActiveNavLinkClass(styles['footer__linksBlockLink'],styles["footer__linksBlockLink_active"])


    return (
        <footer className={styles['footer']}>
            <div className={`container`}>
                <div className={styles["footer__mainBlock"]}>
                    <div className={styles["footer__logoBlock"]}>
                        <Link
                            to={homePagePath}
                            onClick={scrollTop}
                        ><img src={footerLogo} alt="logo" className={styles["footer__logoImg"]}/></Link>
                        <div className={styles["footer__iconsBlock"]}>
                            <a href={socialLinks.facebook} target='_blank' rel="noreferrer" className={styles["footer__iconsLink"]}>
                                <Svg className={styles['footer__iconsImg']} id={facebookIcon}/>
                            </a>
                            <a href={socialLinks.instagram} target='_blank' rel="noreferrer" className={styles["footer__iconsLink"]}>
                                <Svg className={styles['footer__iconsImg']} id={instagramBlackIcon}/>
                            </a>
                            <a href={socialLinks.linkedIn} target='_blank' rel="noreferrer" className={styles["footer__iconsLink"]}>
                                <Svg className={styles['footer__iconsImg']} id={linkedInIcon}/>
                            </a>
                            <a href={socialLinks.behance} target='_blank' rel="noreferrer" className={styles["footer__iconsLink"]}>
                                <Svg className={styles['footer__iconsImg']} id={behanceIcon}/>
                            </a>
                            <a href={socialLinks.upwork} target='_blank' rel="noreferrer" className={styles["footer__iconsLink"]}>
                                <Svg className={styles['footer__iconsImg']} id={upworkIcon}/>
                            </a>
                            <a href={socialLinks.whatsapp} target='_blank' rel="noreferrer" className={styles["footer__iconsLink"]}>
                                <Svg className={styles['footer__iconsImg']} id={whatsappIcon}/>
                            </a>
                        </div>
                        <FooterSubscribeBlock className={styles['footer__subscribeBlock_tab']}/>
                    </div>
                    {
                        footerLinksArr.map((item, index) => (
                            <div className={styles["footer__linksBlock"]} key={index}>
                                <h5 className={styles["footer__linksBlockTitle"]}>{item.title}</h5>
                                {
                                    item.links.map((linkItem, index) => {
                                        if (item.title === 'Contact us') {
                                            return (
                                                <a
                                                    key={index}
                                                    href={linkItem.link}
                                                    className={styles["footer__linksBlockLink"]}
                                                    target={linkItem.link.includes('tel') || linkItem.link.includes('mailto') ? '_self' : '_blank'}
                                                    rel='noreferrer'
                                                >{linkItem.text}</a>
                                            )
                                        } else {
                                            return (
                                                <NavLink
                                                    onClick={scrollTop}
                                                    key={index}
                                                    to={linkItem.link}
                                                    className={deskLinksActiveClass}
                                                >{linkItem.text}</NavLink>
                                            )
                                        }
                                    })
                                }
                            </div>
                        ))
                    }
                    <FooterSubscribeBlock className={styles['footer__subscribeBlock_desk']}/>
                </div>
                <div className={styles["footer__accordionBlock"]}>
                    {
                        footerLinksArr.map((item, index) => (
                            <div className={styles["footer__accordionItems"]} key={index}>
                                <h5
                                    onClick={() => onToggleDropdowns(index)}
                                    className={styles["footer__accordionTitle"]}
                                >
                                    <span className={styles["footer__accordionTitleText"]}>{item.title}</span>
                                    <Svg className={`${styles['footer__accordionTitleIcon']} ${dropdownOpened.includes(index) ? styles['footer__accordionTitleIcon_active'] : ''}`} id={arrowDownIcon}/>
                                </h5>
                                <TransitionProvider
                                    // eslint-disable-next-line react/style-prop-object
                                    style={'height'}
                                    className={styles["footer__accordionContent"]}
                                    inProp={dropdownOpened.includes(index)}
                                >
                                        {item.links.map((link, linkIndex) => {
                                            if (item.title !== 'Contact us') {
                                                return (
                                                    <NavLink
                                                        key={linkIndex}
                                                        to={link.link}
                                                        onClick={scrollTop}
                                                        className={accordionLinksActiveClass}
                                                    >{link.text}</NavLink>
                                                )
                                            } else {
                                                return (
                                                    <a
                                                        key={linkIndex}
                                                        href={link.link}
                                                        className={`${styles["footer__accordionLink"]} ${styles["footer__linksBlockLink"]}`}
                                                    >{link.text}</a>
                                                )
                                            }
                                        })}
                                </TransitionProvider>
                            </div>
                        ))
                    }

                </div>
                <div className={styles["footer__copyrightBlock"]}>
                    <p className={styles["footer__copyrightBlock__text"]}>Copyright &copy; {date.getFullYear()}. All
                        rights reserved.</p>
                </div>
            </div>
        </footer>
    );
}

export default Footer;